.rwt__tabs[data-rwt-vertical="true"] {
  display: flex;
}

.rwt__tablist:not([aria-orientation="vertical"]) {
        border-bottom: 1px solid #ddd;
}

.rwt__tablist[aria-orientation="vertical"] {
  display: flex;
        flex-direction: column;
        flex-shrink: 0;
        flex-grow: 0;
        border-right: 1px solid #ddd;
        margin-right: 1rem;
}

.rwt__tab {
  font-size: 0.875rem;
  line-height: 1.25rem;
  letter-spacing: 0.1px;
  outline: 2px solid transparent;
  outline-offset: -2px;
  position: relative;
  display: flex;
  height: 2rem;
  align-items: center;

        background: transparent;
        border: 0;
        font-family: inherit;
  padding: 1rem 2rem;
  transition: background 0.3s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.rwt__tab[aria-selected="false"]:hover,
.rwt__tab:focus {
  outline: 0;
  background-color: #f4f4f4;
        background-color: rgba(0,0,0,0.05);
}

.rwt__tab[aria-selected="true"] {
        position: relative;
  outline: 0;
  background-color: #e5e5e5;
  font-weight: 600;
}

.rwt__tab[aria-selected="true"]:before {
        content: '';
        position: absolute;
}

.rwt__tablist:not([aria-orientation="vertical"]) .rwt__tab[aria-selected="true"]:after {
  bottom: -1px;
        left: 0;
        width: 100%;
        border-bottom: 4px solid #00d8ff;
}

.rwt__tablist[aria-orientation="vertical"] .rwt__tab[aria-selected="true"]:before {
  left: 1px;
  top: 0;
  height: 100%;
  border-left: 3px solid #0f62fe;
}
